.line {
  height: 0.5px;
  background-color: $blue;
}
.line-top {
  display: flex;
  margin-bottom: 1.5rem;
  font-size: 1.6rem;
  font-weight: bold;
  text-transform: uppercase;
}
.line-bottom {
  display: flex;
  margin-top: 1.5rem;
  font-size: 1.3rem;
  font-weight: 500;
  text-transform: uppercase;
  & > div:first-child {
    width: 33.3333%;
  }
  & > div:last-child {
    width: 66.6666%;
    display: flex;
    justify-content: space-between;
  }
}
.learn-more {
  margin-top: 3rem;
  position: relative;
  border: none;
  white-space: nowrap;
  text-transform: uppercase;
  box-shadow: 0 0 0 1px $blue;
  color: $blue;
  background: none;
  align-self: flex-start;
  padding: 0 30px;
  padding-top: 2px;
  height: 28px;
  font-weight: 600;
  border-radius: 25px;
  overflow: hidden;
  z-index: 1;
  cursor: pointer;
  font-size: 1.2rem;
  &:hover {
    color: $white;
    transition: color $ease-in-soft 0.3s;
  }
  &:before {
    content: "";
    position: absolute;
    background: $blue;
    bottom: 0;
    left: 0;
    right: 0;
    top: 100%;
    z-index: -1;
    transition: all 0.3s;
  }
  &:hover:before {
    top: 0;
    transition: top $ease-in-soft 0.3s;
  }
}

@mixin word($delay) {
  @include typo-heading-1;
  position: relative;
  padding-right: 0.025em;
  display: inline-block;
  overflow: hidden;
  line-height: 0.9;
  font-size: 6.25vw;

  @include mobile {
    font-size: 4.6rem;
  }

  & > span {
    @include tranlate-up-title;
    display: inline-block;
    transform: translateY(103%);
    animation-delay: $delay;
    font-weight: bold;
    color: $blue;
    transition: color 1s;
  }
}

.word-1 {
  @include word(0.6s);
}

.word-2 {
  @include word(0.7s);
}

.circle {
  display: inline-block;
  width: 4.5vw;
  height: 4.5vw;
  min-width: 35px;
  min-height: 35px;
  margin-top: 5px;
  border-radius: 50%;
  background-color: $blue;
  margin-right: 0.3em;
  transition: background-color 1s;
}

.word-3 {
  @include word(0.8s);
  display: flex;
  align-items: center;
  &.right {
    justify-content: flex-end;
  }
}

.word-4 {
  @include word(0.9s);
}

.contents-section {
  box-sizing: border-box;
  background-color: $green;
  color: $blue;
  transition: color 1s, background-color 1s;
  a {
    color: $blue;
    transition: color 1s;
  }
}

.page-content {
  padding: calc(1.5rem + 2.5vw) 5.4vw;
}

.main-banner {
  padding-bottom: 2vw;
}

.title {
  display: flex;
  flex-direction: column;
  margin-top: calc(3rem + 2vw);
  text-transform: uppercase;
}

.smooth-wrapper {
  @include smooth-wrapper(100px);
}

.video {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  margin-bottom: 1rem;
  video {
    width: 100%;
  }
}
.video-label {
  font-size: 0.2rem;
  text-align: right;
  margin-top: 5px;
}

.brand {
  display: flex;
  align-items: center;
  margin-top: 4rem;
}
.brand-description {
  margin-right: auto;
  margin-top: 2rem;
  width: 50%;
  font-size: 3.8rem;
  // font-size: 2.2rem;
  span {
    font-size: 2.2rem;
    vertical-align: top;
    display: inline-block;
    padding-top: 2px;
  }
}
.brand-logo {
  font-size: 200px;
  & > span:first-child {
    font-size: 270px;
    font-family: Broshk, sans-serif;
  }
}
